.container {
  width: 80%;
  margin: 5rem auto;
  background: rgba(235, 235, 255, 0.85);
  border-radius: 2rem;
  border: .3rem solid rgba(35, 35, 211, 0.3);
  box-shadow: 2rem 2rem 7rem rgba(0, 0, 0, 0.5);
}

hr {
  margin-top: 0;
  width: 80%;
  color: black;
  border-top: .3rem solid #2323d3;
  opacity: 0.3;
}

@media screen and (max-width: 1200px) {
	.container {
		width: 90%;
		margin: 3rem auto;
		box-shadow: 1rem 1rem 5rem rgba(0, 0, 0, 0.5);
	}
}

@media screen and (max-width: 1200px) {
	.container {
		width: 95%;
		margin: 2rem auto;
	}
}