div {
  margin: 0;
  padding: 0;
  background: none;
}

.title {
  text-align: center;
  font-size: 5rem;
  margin: 1rem 0;
  text-shadow: .3rem .3rem rgba(255, 255, 255, .7);
}

@media screen and (max-width: 1370px) {
	.title {
		font-size: 4.5rem;
	}
}

@media screen and (max-width: 1200px) {
	.title {
		font-size: 4rem;
	}
}

@media screen and (max-width: 900px) {
	.title {
		font-size: 3.6rem;
	}
}

@media screen and (max-width: 700px) {
	.title {
		font-size: 3.2rem;
	}
}

@media screen and (max-width: 500px) {
	.title {
		font-size: 3rem;
	}
}